import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cards";

import "./Swiper.scss";

// import Swiper core and required modules
import SwiperCore, { EffectCards } from "swiper";

// install Swiper modules
SwiperCore.use([EffectCards]);

export default function App() {
  return (
    <>
      <Swiper effect={"cards"} grabCursor={true} className="mySwiper">
        <SwiperSlide>
          <pr>
            -   , -   ,
            -   , -   ,
            -   , -   ,
            -   , -   !
             
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            Saflarda qad kerib turgan ey askar, Bilgil, mardni korsa, chekinar
            xatar. Yurakni, bilakni etgil safarbar, Gulday diyoringni asra
            hamisha! Duosi, sozlari yodingda tursin, Nur ingan yuzlari yodingda
            tursin, Mehrli kozlari yodingda tursin, Onaizoringni asra hamisha!
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
               ,    ,    
            ,    .    ,  
             ,    ,    .
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            ,    ,   ,
              ,  ,   
            ,   -.
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
             14 !      
                 .     
            .      , 
            ,   ,       ,
          </pr>
        </SwiperSlide>
      </Swiper>
    </>
  );
}
