import card3 from "./img//rabochi fon.png";
import TelegramComments from "react-telegram-comments";
import Swiper from "./Swiper";
const Sher = () => {
  return (
    <div className="m-t" id="sher">
      <h1 className="text-gold">14-Yanvarga sherlar</h1>
      <div className="gv">
        <div className="card">
          <img src={card1} alt="" />
          <Swiper/>
          
        </div>
        <div className="card loong m-t">
          <img src={card2} alt="" />
          <pr>
            Ozbekiston respublikasi qurolli kuchlari  Ozbekiston Respublikasi
            milliy manfaatlarini, suvereniteti, hududiy yaxlitligini hamda
            aholining tinch hayotini himoya qilish, urushlar va qurolli
            mojarolarni qaytarish va oldini olish uchun davlat tomonidan tashkil
            etilgan va saqlab turilgan harbiy birlashmalar, qoshilmalar va
            qismlarni oz ichiga oladi.
          </pr>
        </div>
      
        <div className="hero  m-t ">
          <div>
            <p>Yaratuvchi</p>
            <h1>Omonov</h1>
            <h1>Sanjar</h1>
            <p>TATT, 23-2 -guruh talabasi.</p>
            <a href="https://www.instagram.com/sanjik_sheyh/" className="m-t">Instagram</a>
          </div>

          <img src={card3} alt="" />
        </div>
       
        <div className="hero  m-t ">
          <TelegramComments
            websiteKey="EKuLLw2r"
            customHeight="null"
            commentsNumber={20}
            isDark={false}
            containerClassName="awesome-comments"
            wrapperClassName="awesome-comments__wrapper"
            pageId="/"
          />
        </div>
      </div>
    </div>
  );
};

export default Sher;
